.address-list-wrapper {
  max-width: 1200px;
  margin: 20px auto;
  padding: 1rem;
  background: #f5e653;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-family: Arial, sans-serif;
}

h2 {
  text-align: center;
  color: #0000FF;
}

.table-container {
  overflow-x: auto;
}

.address-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 1rem;
  font-size: 14px;
}

.address-table th,
.address-table td {
  border: 1px solid black;
  padding: 10px;
  text-align: center;
  vertical-align: top;
}

.address-table th {
  background-color: #f7f7f7;
  color: #333;
}

.address-table td {
  word-break: break-word;
}

.address-table td:last-child {
  white-space: nowrap;
  text-align: center;
}

.address-table button {
  margin: 2px;
  padding: 6px 10px;
  font-size: 13px;
  background-color: #007bff;
  border: none;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}

.address-table button:hover {
  background-color: #0000FF;
}
