.address-form, .address-list {
  max-width: 600px;
  margin: 20px auto;
  padding: 1rem;
  background: #f5e653;
  border-radius: 10px;

}

.address-form input {
  display: block;
  width: 90%;
  margin: 8px auto;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.address-item {
  background: white;
  margin: 10px 0;
  padding: 12px;
  border-radius: 6px;
}

.form-buttons {
  display: flex;
  justify-content: center; /* Center buttons horizontally */
  align-items: center;     /* Align buttons vertically (if they have different heights) */
  gap: 10px;
  margin-top: 10px;
}

.form-buttons button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.form-buttons button[type="submit"] {
  background-color: #28a745;
  color: white;
}

.form-buttons button[type="button"] {
  background-color: #dc3545;
  color: white;
}

